.button {
  font-family: "Luckiest Guy", cursive;
  color: #faf4e8;
  background: #655967;
  font-size: x-large;
  border: none;
  border-radius: 10px;
  padding: 0.25rem 1rem;
  cursor: pointer;
}
.button:hover {
  color: #655967;
  background: #faf4e8;
}

.button:focus {
    outline: none;
  }
